<template>
  <v-row>
    <v-col class="py-0 px-4">
      <v-card elevation="0">
        <v-card-title>
          <v-row justify="space-between">
            <v-col cols="12" sm="4">
              История игр
            </v-col>
            <v-col cols="12" sm="4">
              <v-select
                label="Категория" 
                :items="['All', 'Done', 'Process', 'Error']"
                v-model="group_type"
              ></v-select>
            </v-col>
            <v-col cols="12" sm="4">
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Поиск"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-title>
      </v-card>
      <v-data-table
        :headers="headers"
        :items="filteredItems"
        :search="search"
      >
        <template v-slot:item.action>
          <v-btn icon>
            <v-icon small>
              mdi-information-outline
            </v-icon>
          </v-btn>
        </template> 
      </v-data-table> 
    </v-col>
  </v-row>
</template>

<script>
export default {
  data: () => ({
    group_type: null,
    search: '',
    headers: [
      { text: 'ID игры', align: 'left', value: 'gameId' },
      { text: 'ID победителя', value: 'winnerId' },
      { text: 'Дата', value: 'date' },
      { text: 'Имя победителя', value: 'winnerName' },
      { text: 'Шанс победы', value: 'chanceOfVictory' },
      { text: 'Статус', value: 'status' },
      { text: 'Действия', value: 'action', sortable: false }
    ],
    items: [
      { 
        gameId: 1,
        winnerId: 12,
        date: new Date().toISOString().substr(0, 10),
        winnerName: 'Саша',
        chanceOfVictory: 0.42,
        status: 'done'
      },
      { 
        gameId: 2,
        winnerId: 2,
        date: new Date().toISOString().substr(0, 10),
        winnerName: 'Ваня',
        chanceOfVictory: 0.61,
        status: 'done'      
      },
      { 
        gameId: 3,
        winnerId: 43,
        date: new Date().toISOString().substr(0, 10),
        winnerName: 'Катя',
        chanceOfVictory: 0.92,
        status: 'process'
      },
      { 
        gameId: 4,
        winnerId: 3,
        date: new Date().toISOString().substr(0, 10),
        winnerName: 'Даша',
        chanceOfVictory: 0.01,
        status: 'done' 
      },
      { 
        gameId: 5,
        winnerId: 6,
        date: new Date().toISOString().substr(0, 10),
        winnerName: 'Леша',
        chanceOfVictory: 0.94,
        status: 'done' 
      },
      { 
        gameId: 6,
        winnerId: 24,
        date: new Date().toISOString().substr(0, 10),
        winnerName: 'Андрей',
        chanceOfVictory: 0.25,
        status: 'process'
      }          
    ]
  }),
  computed: {
    filteredItems() {
      return this.items.filter((item) => {
        return (this.group_type == 'All') ? item : !this.group_type || (item.status.toLowerCase() === this.group_type.toLowerCase());
      });
    }
  }
}
</script>
